.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #b1b2b3;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn-block{
  display: flex;
  flex-direction: row;
  gap: 45px;
}

.btn{
  width: 150px;
  height: 50px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  background-color: orange;
  color: white;
  font-size: 18px;
  font-weight: 600;
}

.btn:hover{
  transform: scale(1.1);
}